.brands {
  display: flex;
  flex-direction: column; }
  .brands::after {
    content: "";
    margin-top: 24px;
    border-bottom: 1px solid  #D9FFF5; }
  .brands__title {
    max-width: 100%;
    background: linear-gradient(90deg, #f8f8f8 0%, #ffffff 98.61%);
    text-transform: uppercase;
    padding: 16px;
    align-items: center;
    max-height: 80px;
    font-size: 16px;
    line-height: 24px;
    font-weight: 500; }
  .brands__items {
    display: none; }
  .brands__item {
    display: inline-flex;
    width: 206px;
    height: 80px;
    justify-content: space-between;
    background-color: #ffffff;
    border: 1px solid #eaeaea;
    border-radius: 6px;
    align-items: center;
    margin: 0 16px;
    padding: 0 16px; }
  .brands .btn {
    border-radius: 50%;
    width: 40px;
    height: 40px; }
    .brands .btn__brand {
      background-color: #fff;
      border: 2px solid #ff3e79; }
    .brands .btn__brand::after {
      content: '\276F';
      width: 20px;
      height: 20px;
      color: #ff3e79; }
    .brands .btn:hover {
      opacity: 0.5; }
  .brands__more {
    display: none; }
  .brands .swiper {
    width: 100vw;
    height: 144px; }
    .brands .swiper .swiper-pagination-bullet {
      background-color: #b5b6bc;
      width: 12px;
      height: 12px; }
  @media (min-width: 768px) {
    .brands .swiper {
      display: none; }
    .brands .brands {
      display: block;
      min-height: 332px; }
      .brands .brands__items {
        display: flex;
        flex-wrap: wrap;
        margin: 0 12px;
        overflow-y: hidden;
        height: 190px;
        transition: height 1s; }
      .brands .brands__open {
        overflow: visible;
        height: 380px; }
      .brands .brands__item {
        width: 224px;
        height: 72px;
        margin: 12px; }
      .brands .brands__more {
        display: flex;
        width: 130px;
        margin: 0 30px; }
        .brands .brands__more::before {
          float: left;
          content: '';
          background: url(./static/arrows.svg) no-repeat;
          width: 10px;
          height: 10px;
          color: #41f6d7;
          margin-right: 16px;
          align-self: center;
          transition: 1s; } }
  @media (min-width: 1120px) {
    .brands .brands__items {
      height: 180px; }
    .brands .brands__item {
      width: 240px;
      margin: 8px 16px; }
    .brands .brands__more {
      margin: 0 40px; }
    .brands .brands__open {
      overflow: visible;
      height: 265px; } }
