.slide-menu {
  overflow-x: auto;
  height: 100%;
  display: flex;
  flex-wrap: nowrap;
  margin: 32px 0;
  align-items: center; }
  .slide-menu__item {
    padding: 8px 15px;
    white-space: nowrap;
    color: #7e7e82;
    align-items: center;
    border-radius: 6px; }
    .slide-menu__item:hover, .slide-menu__item:focus {
      box-shadow: inset 0 0 0 2px #b8ffec; }
  @media (min-width: 1120px) {
    .slide-menu {
      flex-wrap: wrap;
      overflow: visible; } }
