.prices {
  display: flex;
  flex-direction: column;
  padding: 2rem 0; }
  .prices::after {
    content: "";
    border-bottom: 1px solid  #D9FFF5; }
  .prices__title {
    max-width: 100%;
    background: linear-gradient(90deg, #F8F8F8 0%, #FFFFFF 98.61%);
    text-transform: uppercase;
    padding: 16px;
    align-items: center;
    max-height: 80px;
    font-size: 16px;
    line-height: 24px;
    font-weight: 400; }
  .prices__card {
    height: 200px;
    width: 260px;
    margin: 8px;
    padding: 16px;
    background: #FFFFFF;
    border: 1px solid #EAEAEA;
    border-radius: 6px;
    position: relative; }
    .prices__card__title {
      font-family: TTLakes, sans-serif;
      line-height: 24px;
      font-weight: 200;
      opacity: 0.5; }
    .prices__card__content {
      margin-bottom: 16px; }
  .prices .btn {
    width: 119px;
    height: 32px;
    background-color: #FF3E79;
    border-radius: 16px;
    color: #FFFFFF;
    font-family: TTLakes-Bold, sans-serif;
    text-transform: uppercase;
    font-size: 12px;
    padding: 0 12px;
    display: flex;
    align-items: center;
    justify-content: space-between; }
    .prices .btn::after {
      content: "\276F";
      width: 20px;
      height: 20px;
      color: #FFFFFF; }
    .prices .btn:hover {
      opacity: 0.5; }
  .prices__btn {
    position: absolute;
    bottom: 16px;
    right: 16px; }
    .prices__btn__offer {
      position: static;
      display: flex;
      justify-content: flex-start;
      padding: 1.2rem;
      font-family: 'TTLakes-Medium', sans-serif;
      margin-bottom: 40px; }
      .prices__btn__offer::after {
        content: "\276F";
        width: 20px;
        height: 20px;
        color: #FF3E79;
        margin-left: 10px; }
  .prices__swiper {
    overflow: hidden; }
  .prices__swiper-pagination {
    display: flex;
    justify-content: center; }
  .prices .swiper-pagination-bullet {
    background-color: #b5b6bc;
    width: 12px;
    height: 12px; }
  .prices__content {
    display: none; }
  .prices__text {
    padding: 1.2rem;
    line-height: 20px; }
  @media (min-width: 768px) {
    .prices__swiper {
      display: none; }
    .prices__table {
      width: 720px;
      margin: 0 auto; }
    .prices__content {
      display: flex;
      flex-direction: column;
      border-collapse: collapse;
      width: 100%; }
    .prices__head {
      display: flex;
      justify-content: space-between;
      padding: 31px 18px 16px; }
      .prices__head th {
        background-color: #F8F8F8;
        opacity: 0.5;
        text-align: left;
        width: calc(100%/4);
        padding-right: 16px; }
        .prices__head th:first-child {
          min-width: 350px; }
        .prices__head th:last-child {
          visibility: hidden; }
    .prices__items {
      background-color: #FFFFFF;
      justify-content: space-between;
      border-radius: 6px;
      padding: 0 16px; }
    .prices__item {
      border-bottom: 1px solid #F8F8F8;
      justify-content: space-between; }
      .prices__item td {
        height: 60px;
        white-space: nowrap;
        width: calc(100%/4);
        padding-right: 16px; }
        .prices__item td:nth-child(1) {
          flex-shrink: 0; }
        .prices__item td:last-child {
          padding-right: 0; }
    .prices .btn {
      position: static; }
    .prices__text {
      width: 455px; } }
  @media (min-width: 1128px) {
    .prices__table {
      width: 1056px; }
    .prices__head {
      width: 100%; }
      .prices__head th:first-child {
        width: 40%; }
      .prices__head th:last-child {
        width: 10%; }
    .prices__items {
      display: flex;
      flex-direction: column; }
    .prices__item {
      display: flex;
      justify-content: space-between;
      width: 100%; }
      .prices__item td {
        display: flex;
        align-items: center; }
        .prices__item td:first-child {
          width: 40%; }
        .prices__item td:last-child {
          width: 10%; } }
