.footer {
  font-family: 'TTLakes-Medium', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .footer__item {
    text-align: center;
    margin-bottom: 1rem;
    opacity: 0.5; }
  @media (min-width: 768px) {
    .footer {
      padding: 1.5rem;
      flex-direction: row;
      justify-content: space-between; }
    .footer__item {
      width: 218px; }
      .footer__item:first-child {
        text-align: left; }
      .footer__item:last-child {
        text-align: right; } }
  @media (min-width: 1126px) {
    .footer__item:last-child {
      width: 330px; } }
