.content__text {
  font-size: 14px;
  line-height: 18px;
  padding: 10px 15px;
  letter-spacing: 0.2px;
  width: 312px; }
  .content__text .text {
    overflow: hidden;
    height: 90px;
    margin-bottom: 15px;
    transition: height 1s; }
  .content__text .open {
    overflow: visible;
    height: 15em; }

.content__more {
  display: flex;
  margin: 10px 15px;
  color: #1b1c21;
  font-weight: 500; }
  .content__more::before {
    float: left;
    content: '';
    background: url(./static/arrows.svg) no-repeat;
    width: 10px;
    height: 10px;
    color: #41f6d7;
    margin-right: 16px;
    align-self: center;
    transition: 1s; }

.content__photo {
  display: flex;
  height: 210px;
  width: 312px;
  border: 2px solid #ffffff; }
  .content__photo img {
    width: 312px; }

@media (min-width: 768px) {
  .content {
    display: flex;
    justify-content: space-between;
    padding: 24px 0;
    margin: 0 24px; }
  .content__text {
    padding: 0; }
    .content__text .text {
      height: 180px; }
      .content__text .text p {
        margin-bottom: 30px; }
    .content__text .open {
      overflow: visible;
      height: 17em; }
  .content__photo {
    width: 360px;
    height: 210px;
    border: none; }
    .content__photo img {
      border-radius: 6px;
      width: 360px; } }

@media (min-width: 1120px) {
  .content__text {
    width: 490px;
    line-height: 20px; }
  .content__photo {
    width: 532px;
    height: 307px; }
    .content__photo img {
      width: 532px; } }

.rotate::before {
  transform: rotate(180deg); }
