.service {
  display: flex;
  flex-direction: column;
  padding: 30px 0 0; }
  .service::after {
    content: "";
    margin-top: 24px;
    border-bottom: 1px solid  #D9FFF5; }
  .service__title {
    max-width: 100%;
    background: linear-gradient(90deg, #F8F8F8 0%, #FFFFFF 98.61%);
    text-transform: uppercase;
    padding: 16px;
    align-items: center;
    max-height: 80px;
    font-size: 16px;
    line-height: 24px;
    font-weight: 400; }
  .service__swiper {
    overflow: hidden;
    width: 100vw; }
  .service__swiper-pagination {
    display: flex;
    justify-content: center; }
  .service .swiper-pagination-bullet {
    background-color: #b5b6bc;
    width: 12px;
    height: 12px; }
  .service__items {
    display: none; }
  .service__item {
    width: 240px;
    height: 160px;
    margin: 16px;
    background: #FFFFFF;
    border: 1px solid #EAEAEA;
    border-radius: 6px;
    position: relative; }
    .service__item__title {
      font-family: TTLakes-Bold, sans-serif;
      font-size: 16px;
      line-height: 24px;
      padding: 16px; }
  .service .btn {
    border-radius: 50%;
    width: 40px;
    height: 40px; }
    .service .btn:hover {
      opacity: 0.5; }
  .service__btn {
    background-color: #fff;
    border: 2px solid #FF3E79;
    margin: 0;
    position: absolute;
    bottom: 16px;
    right: 16px; }
    .service__btn::after {
      content: "\276F";
      width: 20px;
      height: 20px;
      color: #FF3E79; }
  .service__more {
    display: none; }
  @media (min-width: 768px) {
    .service__swiper {
      display: none; }
    .service__items {
      display: flex;
      flex-wrap: wrap;
      overflow-y: hidden;
      height: 192px;
      transition: 1s; }
    .service__open {
      overflow: visible;
      height: 400px; }
    .service__item {
      width: 224px; }
    .service__more {
      display: flex;
      margin: 0 30px; }
      .service__more::before {
        float: left;
        content: '';
        background: url(./static/arrows.svg) no-repeat;
        width: 10px;
        height: 10px;
        color: #41f6d7;
        margin-right: 16px;
        align-self: center;
        transition: 1s; } }
  @media (min-width: 1128px) {
    .service__item {
      width: 240px; } }
